nav{
    background: rgba(0, 0, 0,0.3);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    /* left: 50%;/ */ left: calc(50% - 11rem);
    /* right: 50%; */
    /* transform: translate(-50%); */
    bottom: 1rem;
    gap: 0.8rem;
    display: flex;
    border-radius: 3rem;
    backdrop-filter: blur(15px);


}
nav a{
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.3rem;


}
nav a:hover{
    background: rgba(0, 0, 0,0.3);
}
nav a.active{
    background: var(--color-primary);
    color: var(--color-bg);
    /* font-size: 1.3rem; */
}